.sort {
    margin-top: 40px;
    margin-left: 20px;
    position: relative;
    &__label {
      display: flex;
      align-items: center;
  
      svg {
        margin-right: 8px;
        transform: rotate(180deg);
  
        &.rotated {
          transform: rotate(0);
        }
      }
  
      b {
        margin-right: 8px;
      }
  
      span {
        color: #fe5f1e;
        border-bottom: 1px dashed #fe5f1e;
        cursor: pointer;
      }
    }
  
    &__popup {
      position: absolute;
      right: 0;
      margin-top: 15px;
      background: #ffffff;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
      border-radius: 10px;
      overflow: hidden;
      padding: 10px 0;
      width: 160px;
  
      ul {
        overflow: hidden;
        li {
          padding: 12px 20px;
          cursor: pointer;
  
          &.active,
          &:hover {
            background: rgba(254, 95, 30, 0.05);
          }
  
          &.active {
            font-weight: bold;
            color: #fe5f1e;
          }
        }
      }
    }
  }