.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    //transform: scale(0);
}

.modal.active {
    opacity: 1;
    pointer-events: all;
    //transform: scale(1);
}

.modal__content {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    width: 75vh;
    &-img {
        width: 100%;
        height: 100%;
    }
}

.modal__content.active {
    transform: scale(1);
}